.card {
    position: relative;
  
    
}
.card img{
    width: 100%;   
    display: block;
    border-radius: 6px;
    border: 1px solid rgb(0, 81, 135);
}

/* front of card - the picture */

.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}
/* back of card - the picture */

.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}